* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  font-family: "Roboto", sans-serif;
  color: #010c39;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

h1, h2, h3, h4 {
  font-weight: normal;
}

img, svg {
  max-height: 100%;
  max-width: 100%;
  width: auto;
  height: auto;
}

input, button, textarea {
  outline: none;
}

input:focus, button:focus, textarea:focus {
  outline: none;
}

input, textarea {
  padding: 10px;
}

textarea {
  resize: none;
}

ul, ol {
  list-style: none;
  margin-bottom: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  color: #408de4;
}

button, .btn {
  border: 2px solid #408de4;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  border-radius: 5px;
  min-width: 120px;
  background: #408de4;
  color: #ffffff;
  position: relative;
  overflow: hidden;
}

button:hover, .btn:hover {
  background: #fff;
  border-color: #408de4;
  color: #408de4;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.15);
}

.animate, a, button, .btn {
  transition: all 0.3s ease-out;
}

.hidden {
  display: none !important;
}

.img_cover {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100%;
}

.img_bg_cover {
  position: relative;
}

.img_bg_cover:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}

.img_bg_cover .container {
  position: relative;
}

.container {
  max-width: 1280px;
