}

.menu_mob {
  display: none;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  height: 100vh;
  overflow: auto;
}

.burger {
  height: 25px;
  width: 30px;
  position: relative;
  transition: 0.5s ease-in-out;
  cursor: pointer;
  z-index: 1;
  display: none;
}

.burger span {
  position: absolute;
  height: 4px;
  width: 100%;
  background: #fff;
  border-radius: 4px;
  opacity: 1;
  left: 0;
  transition: 0.25s ease-in-out;
}

.burger span:nth-child(1) {
  top: 0;
}

.burger span:nth-child(2),
.burger span:nth-child(3) {
  top: 10px;
}

.burger span:nth-child(4) {
  top: 20px;
}

.burger span:nth-child(5) {
  top: 10px;
}

.burger span:nth-child(6) {
  top: 10px;
}

.burger span:nth-child(7) {
  top: 10px;
}

.burger.active span:nth-child(1) {
  top: 10px;
  width: 0%;
  left: 50%;
}

.burger.active span:nth-child(2) {
  transform: rotate(45deg);
}

.burger.active span:nth-child(3) {
  transform: rotate(-45deg);
}

.burger.active span:nth-child(4) {
  top: 10px;
  width: 0%;
  left: 50%;
}

.burger.active span:nth-child(5) {
  transform: rotate(-45deg);
}

.burger.active span:nth-child(6) {
  transform: rotate(-45deg);
}

.burger.active span:nth-child(7) {
  transform: rotate(-45deg);
}

